// Buttons Mixins
.cta-size() {
    padding: 10px 50px;
    font-weight: bold;
    font-size: 1.3em;
    border-radius: 50px;
}

.cta-shape(@border-color,@r) {
    border: 2px solid @border-color;
    border-radius: @r;
}

// form mixins
.form-el(@r) {
    border: 1px solid gray;
    border-radius: @r;
}

@primary-btn: rgb(253, 216, 1);